div {
    box-sizing: border-box;
}
div.row{
    box-sizing: border-box;
    display: inline-block;
    height: 500px;
    width: 250px;
    position: relative;
}

body{
    margin: 0;
    padding-top: 30px;
    align-content: center;
    text-align: center;
    background-color: thistle;
    width: 100%;
}
.big-box{
    margin: 0 auto;
    width: 1000px;
}

.container {
    width: 500px;
    height: 50px;
    border: 3px solid green;
    margin: 0 auto;
}
.lc{height: 50px; width: 250px;}
.container.dm {border: 3px solid #712b06;}
.wc{width: 350px}

#keyCont{
    min-width: 350px;
    width: 40%;
    max-width: 520px;
    height: 152px
}

#short{height:40px;background-color: green;}
.dm#short{background-color: #712b06;}

p{
    border: 2px solid black;
    box-sizing: border-box;
    margin: 0;
    width:100%;
    height: 100%;
    font-family: "American Typewriter", serif;
    font-size:200%;
    display: inline-block;
}

.title-box{
    font-family: "Times New Roman", serif;
    font-size:230%;
    display: block;
}

#tB{background-color: lightgray;}
#gTB{background-color: limegreen;}
#rTB{background-color: indianred;}
.dm#tB{background-color: #262626;color:pink;}
.dm#gTB{background-color: #104c10;color: white;}
.dm#rTB{background-color: #570909; color: #000000
}

.letter{width: 20%;}

#l-w{background-color: lightgrey;}
#l-e{background-color: lightgreen;}
#l-c{background-color: yellow;}
.dm#l-w{background-color: #262626;color:pink;}
.dm#l-e{background-color: #004500;color:white;}
.dm#l-c{background-color: orange;color: #892d2d;}
input {
    border-radius: 2px;
    margin: 0 auto;
    background-color: #313131;
    display: inline-block;
    color: pink;
    font-family: "American Typewriter", serif;
    font-size: larger;
    text-align: center;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
}

.word {
    text-align: left;
    text-indent: 26px;
    letter-spacing: 56px;
    caret-color: transparent;
    user-focus: disabled;
}

.word.dm {
    border-color: black;
    background-color: #262626;
}

.key {
    border-color: inherit;
    width: 10%;
    color: pink;
    background-color: #313131;
}

.key2 {
    width: 11.11%;
    border-color: inherit;
}

.key3 {
    width: 10%;
    border-color: inherit;
    /*background-image:*/
    /*        conic-gradient(*/
    /*                rgb(0, 204, 136) 0deg 90deg,*/
    /*                rgb(156, 163, 175) 90deg 180deg,*/
    /*                rgb(255, 204, 0) 180deg 270deg,*/
    /*                rgb(156, 163, 175) 270deg 360deg*/
    /*        );*/
}


.grey {
    background-color: grey;
    color: pink
}

.yellow {
    background-color: yellow;
    color: black
}

.green {
    background-color: lightgreen;
    color: black
}

.dm.grey {
    background-color: #110f0f;
    color: #610505
}

.dm.yellow {
    background-color: orange;
    color: white
}

.dm.green {
    background-color: darkgreen;
    color: #ffffff
}

#special-d {
    box-sizing: border-box;
    width: 15%;
    float: right;
    background-image: url(/gh-page/static/media/backspace-icon.2d22e2c0.png);
    background-repeat: no-repeat;
    background-position: center;
}

#special {
    box-sizing: border-box;
    width: 15%;
    padding-top: 1px;
}

.key-row {
    box-sizing: border-box;
    height: 33.33%;
    margin: 0 auto;
    border-color: inherit;
}

.key-box {
    height: 100%;
    width: 100%;
    max-width: 520px;
    border-color: thistle;
}

.key-box.dm {
    border-color: black;
}


button {
    border-radius: 2px;
    border-width: 2px;
    box-sizing: border-box;
    font-size: 130%;
    font-family: "Times New Roman", serif;
    width: 33.33%;
    height: 100%;
    display: inline-block;
    border-color: black;
}

button:hover {
    background-color: thistle;
    -webkit-transform: scale3d(0.98, 0.96, 1);
            transform: scale3d(0.98, 0.96, 1);
}

button.dm {
    background-color: #313131;
    color: #ffcfcf;
    border-color: #000000;
}

button:hover.dm {
    background-color: #761919;
    color: #ff6982;
    -webkit-transform: scale3d(0.96, 0.96, 1);
            transform: scale3d(0.96, 0.96, 1);
}

#ans-box {
    width: 100%;
    user-focus: disabled;
    font-size: 65%;
    background-color: #262626;
    color: pink;
    border-color: black;
    font-stretch: condensed;
    text-indent: -9999px;

}
